@import url(https://fonts.googleapis.com/css2?family=Neuton:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  overflow-x: hidden;
}

.background {
    display: block;
    width: 100vw;
    height: 100vh;
    background: url(https://wallpaperaccess.com/full/1812893.jpg);
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

Header {
    color: white;
    transition: height 1s;
}

section {
    height: 100vh;
}

.App section:not(:first-of-type) {
    background: hsl(0, 0%, 74%);
}

.text.fixed {
    transform: translateY(8vh);
}

.content {
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    letter-spacing: 1px;
}

.content .text > p:nth-child(1) {
    font-size: 3vw;
    font-family: 'Neuton', serif;
}

.content .text > p:nth-child(2) {
    font-size: 3.2vw;
    font-family: 'Neuton', serif;
}

.spacer {
    height: 10%;
}

header#main {
    display: block;
    width: 100%;
    height: 8vh;
}

header > ul {
    width: 40%;
    height: 100%;
    margin: auto;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

header > ul > li {
    flex: 0 1;
    display: inline;
}

header li > p {
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
}

header {
    animation-name: move-back;
    animation-duration: 0.25s;
    color: white;
}

header.fixed {
    position: fixed;
    top: 0;
    z-index: 100;
    background: white;
    color: black;
    animation-name: move-to-fixed;
    animation-duration: 0.3s;
}

li.colored {
    animation-name: hover-animation;
    animation-duration: .5s;
    color: green;
}

header#main.hovered li.colored {
    color: white;
}

header#main.fixed.hovered li.colored {
    color: black;
}

header li {
    transition: color .3s;
}

li:hover {
    /*animation-name: hover-animation;
    animation-duration: .3s;*/
    color: green;
}

@keyframes move-to-fixed {
    from {
        transform: translateY(-8vh);
    }
    to {
        transform: translateY(0);
    }
}

@keyframes move-back {
    from {
        transform: translateY(6vh);
        background: rgba(255, 255, 255, 255);
    }
    to {
        transform: translateY(0);
        background: rgba(255, 255, 255, 0);
    }
}

@keyframes hover-animation-white {
    from {
        color: white;
    }
    to {
        color: green;
    }

}


